<template>
<router-view></router-view>
</template>


<script>
export default{
	data(){
		return{
			themeMode:'dark'
		}
	}, 
	methods:{
		getMediaPreference() {
        const hasDarkPreference = window.matchMedia( "(prefers-color-scheme: dark)").matches;
        this.themeMode = hasDarkPreference ? "dark" : "light"
		document.body.classList.add(this.themeMode);
},
  
	},
	mounted(){
		this.getMediaPreference()
	}
}
</script>

