<template>
    <!-- <div class="setTimer"
    v-if="!timerSet">
    <div @click="startTimer(0.05)">1:00</div>
    <div @click="startTimer(3)">3:00</div>
    <div @click="startTimer(5)">5:00</div>
    </div> -->
    <div class="timer">
        <span
    v-if="timerSet">
    {{ text || "--:--"}}
    </span>
    <div v-if="timerSet" class="buttons">
        <div @click="stopTimer()" class="stop">Stop</div>
        <div @click="pauseTimer()" class="pause">{{timerPaused ? "Play" : "Pause"}}</div>
    </div>
    </div>


  </template>
  
  <script>

export default {
    data (){
        return{
            timeInSecs: 0,
            text: "",
            Secs: 0,
            trick: 0, 
            timerSet: false,
            timerOut: false,
            timerPaused: false
        }
    },
    methods:{
         startTimer(sec) {
            
           this.timerSet=true
           this.trick = setInterval(function(){
            if(!this.timerPaused){
                let secs = this.timeInSecs;
                this.Secs = secs;
            if (secs > 0) 
                this.timeInSecs--; 
            else
                this.stopTimer;

            let mins = Math.floor(secs/60);

            secs %= 60;

            this.text = ( (mins < 10) ? "0" : "" ) + mins + ":" + ( (secs < 10) ? "0" : "" ) + secs;  
            }


            }.bind(this), 1000); 
        },
        stopTimer(){
            clearInterval(this.trick)
            this.text=""
            this.timerSet=false;
            this.timerPaused=false;
            this.timerOut=true;
            let timer = this.timerOut
            let secs =this.Secs
            this.$emit('timerOut', {timer, secs} )
        },
        pauseTimer(){
            this.timerPaused = !this.timerPaused
            this.$emit('timerPaused', this.timerPaused)
        },
        startInput(time){
            this.timeInSecs = time
            this.startTimer(time)
        }

    }
}
  </script>
  <style scoped>
  div{
    display: flex;
    align-items: center;
  }
    .timer{
        flex-direction: column;
        font-size: 3em;
        margin-top: 0.5em;
        gap: 0.5em;
    }
    .buttons{
        gap: 1em;

    }
    .buttons>div{
        cursor: pointer;
    }
  
  </style>