<template>
    <section>
        <h1>Free Typing Test - Typing Speed Tests in {{time}} Seconds.</h1>
        <div class="statics"
        v-if="timerOut"
        >
            <div class="corect">{{ score  }} <br><br> CHARACTERS:</div>
            <div class="mistake">{{ mistake  }} <br><br> ERRORS:</div>
            <div class="mistake">{{Math.floor((180 - secs) / word )  || 0 }} <br><br> WORDS PER MINUTES:</div>

        </div>
        <button 
        @click="startTest"
        v-if="timerOut"
        >Try Again</button>

        <div class="time"
        :class="{active: timerSet}"
        >
            <h3 v-if="!timerOut && timerSet">TIME:</h3>
            <Timer 
            ref="timerComponent"
            @timerOut="timerOutF"
            @timerSet="timerSetF"
            @timerPaused="timerPausedF"></Timer>
        </div>

        <input type="text" v-model="userinput" @keyup.enter="textChanged()"
        placeholder="Type text"
        :disabled="timerPaused || timerOut"
        autofocus>

    <textArea
    :txtArr=outputArr
    :disabled=timerPaused
    ></textArea>
    </section>


</template>
<script>

import textArea from"@/components/UI/textArea"
import Timer from"@/components/UI/Timer"

import words from "@/assets/words.json"
import { ref } from 'vue'

export default{

    components: { textArea,Timer},


    data(){
        return{
            inputArr:words,
            outputArr: [],
            userinput: '',
            currentWord: '',
            score: 0,
            mistake: 0,
            word: 0,
            time:180,
            secs: 0,
            timerOut: false,
            timerSet:false,
            timerPaused: false
        }
    }, 

    methods: {
        timerOutF(event){
            console.log(event)
            this.timerOut = event.timer
            this.secs = event.secs
            this.timerSet = false;
        },
        timerSetF(event){
            this.timerSet = event
        },
        timerPausedF(event){
            this.timerPaused = event
        },
        addText (event){
            this.outputArr = event
            this.currentWord = this.outputArr[0]
        }, 

        textChanged(){
            if(this.userinput != this.currentWord){
                for (let i = 0; i < this.userinput.length; i++) {
                   if(this.userinput[i] != this.currentWord[i])
                        this.mistake++
                        else
                        this.score++
                }

                this.mistake += this.currentWord.length - this.userinput.length
                this.nextStep()
            }
        },
         nextStep(){
            this.outputArr.shift()
            this.currentWord = this.outputArr[0]
            this.userinput= ""
         },
         textGenerate(){
            this.outputArr = []
            for(let i = 0; i < 300; i++){
                let index = Math.floor((Math.random() * this.inputArr.length + 1)  )
                this.outputArr.push(this.inputArr[index])
            }
         },
         startTest (){
            window.location.reload();
         },
    },
     watch: {
        userinput: function(val, oldVal) {
            if(!this.timerSet)
                this.$refs.timerComponent.startInput(this.time)
            this.timerSet = true 


        if (val ===  this.currentWord){
            this.score += this.currentWord.length
            this.word++
            this.nextStep()
        }
    }
  },

  mounted(){
    this.textGenerate()
    this.currentWord = this.outputArr[0]
    console.log(this.themeMode)
},
 
}

</script>

<style scoped>

section{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3em;
}

body.light .statics>div,  body.light button, body.light input{
    background-color: #d2d2d7;
    color:black;

}
h1, h3, p, div, button{
    text-align: center;
}
h1{
    font-size: 5.2em;
    margin-top: 6rem;

}
h3{
    font-size: 3em;
}
.time{
    display: none;
}
.time.active{
    display: block;
}

button{
    border: none;
    cursor: pointer;
    color: #d2d2d7;
}
input{
    background-color: #40414f;
    color: white;
    max-width: 1500px;
    width: calc(100% - 6em);
    margin: 0 6em;
    border: none;
    border-radius: 10px;
    font-size: 2.5em;
    padding: 1em 0.5em;
    outline: none;
}
.statics{
    display: flex;
    gap: 4em;
}
.statics>div, button{
    font-size: 2em;
    padding: 1em;
    background-color: #40414f;
    border-radius: 10px;
}
</style>